import KinoloungeNavBar from "../../Components/KinoloungePage/KinoloungeNavbar";

export default function ShawShowcasePage() {
  return (
    <>
      <KinoloungeNavBar />
      <h1 className="kinolounge-header">Shaw Showcase</h1>
      <div className="page-header-content">
        <p />
        Movies you missed previously
        <p />
        Every year, with so many movies hitting the theatres, it can be hard to
        keep track of everything.
        <p />
        More often, while you are busy fulfilling your worldly obligations and
        attending to life priorities, these movies come and go even before you
        know it.
        <p />
        Fret not. Shaw Showcase will offer a curated selection of some of the
        most-loved indie movies here. Whats more! Grab your blankets and get
        scared silly with our specially selected horror titles.
        <p />
        And yes, we heard you. You could now rent each title starting from $4.99
        and watch it from the comfort of your couch.
        <p />
        So look out for more titles in the coming months.
      </div>
    </>
  );
}
